import { required, email, max, min } from "vee-validate/dist/rules";
import { extend } from "vee-validate";

extend("required", {
  ...required,
  message: "   "
});
extend('password', {
  params: ['target'],
  validate(value, { target }) {
    return value === target;
  },
  message: '   '
});
extend("max", {
  ...max,
  message: "       {length}"
});
extend("min", {
  ...min,
  message: "      {length}"
});
extend("email", {
  ...email,
  message: "      "
});



